html {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: Whitney-normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  background: rgb(50, 61, 77);
  color: white;
  text-align: center;
}

#root {
  color: black;
  background: rgb(50, 61, 77);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  border-radius: 4px;
  background-color: rgb(255, 184, 131);
  border: 2px solid rgb(53, 57, 67);
  font-size: 1vw;
}

button p {
  margin: 0;
}

button:focus {
  outline: none;
}

button:hover {
  background-color: rgb(222, 177, 143);
}

button:active {
  position: relative;
  top: 1px;
}